.TodoItem {
    background-color: #020202;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 24px;
    box-shadow: 0px 5px 50px rgba(153, 152, 152, 0.322);
    
  }
  
  .TodoItem-p {
    margin: 24px 0 24px 24px;
    width: calc(100% - 120px);
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }
  .TodoItem-p--complete {
    text-decoration: line-through;
  }
  
  .Icon-container {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 48px;
    width: 48px;
    font-size: 24px;
    font-weight: bold;
    /* background-color: #CCC; */
  }
  
  .Icon-container-check {
    position: absolute;
    left: 12px;
  }
  .Icon-container-check--active {
    color: #4caf50;
  }
  
  .Icon-container-delete {
    position: absolute;
    top: -24px;
    right: 0;
  }
  .Icon-container-delete:hover {
    color: red;
  }

  .Icon-svg{
    width:24px;
    height: 24px;
  }